.checkbox {
	position: relative;
	z-index: -1;
	opacity: 0; }
.checkbox + label {
	position: relative;
	cursor: pointer; }
.checkbox + label:before {
	content: '';
	position: absolute;
	top: -9px;
	left: -4px;
	width: 1em;
	height: 1em;
	border-radius: 4px;
	background: #FFC800;
	transition: .2s; }
.checkbox:checked + label:before {
	background-image: url(../../images/ic_check.png) {
    @include bg; } }
